// widgets spacing
$widget-bottom-margin: 50px;
$widget-bottom-padding: 5px;
$widget-inner-padding: 10px;
$widget-title-margin: 12px;

// widget color
$widget-background-color: transparent;

// list items dimensions
$widget-list-item-height: 17px;
$widget-list-item-padding: 10px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: $default-text-color;
$widget-heading-color: $default-heading-color;

// links
$widget-link-color: $default-text-color;
$widget-link-hover-color: $default-heading-color;

// specific widgets
$widget-search-form-height: 58px;
$widget-tag-cloud-height: 25px;

aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $widget-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {
        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.mkdf-sidebar,
.wpb_widgetised_column {
    @import '../widget-types/default-wordpress-widgets';
    @import '../widget-types/default-wordpress-widgets-flat';

    .widget {
        &.widget_arabesque_instagram_widget {
            .mkdf-widget-title-holder {
                margin-bottom: 21px;
            }
        }
    }
}