.single-events {

	.mkdf-ttevents-single {
		padding-right: 18px;
	}

	.mkdf-ttevents-single-image-holder {
		margin-bottom: 45px;

		img {
			display: block;
		}
	}

	.tt_event_columns{

		.tt_event_column_left{

			width:45%;
			margin-right: 0;
			padding-left: 15px;
			padding-right: 15px;
			box-sizing: border-box;
		}
		.tt_event_column_right{

			width:55%;
			padding-left: 15px;
			padding-right: 15px;
			box-sizing: border-box;

			h4{
				font-family: $default-heading-font;
				font-weight	: 600;
				font-size:30px;
				color:$default-heading-color;
				margin-top: 33px;
			}

			p {
				color:$default-heading-color;
				font-size: 16px;
				font-family: $default-text-font;
			}

		}
	}


	.mkdf-ttevents-single-title {
		margin-top: 0;
		margin-bottom: 0;
	}

	.mkdf-event-single-icon {
		float: left;
		margin-right: 10px;
		color: $first-main-color;
		font-size: 16px;
	}

	.mkdf-ttevents-single-subtitle {
		margin: 0 0 17px;
		color: $first-main-color;
	}

	.mkdf-ttevents-single-content {
		margin-top: 6px;
	}

	h3.tt_event_margin_top_27 {
		margin-top: 0 !important;
		margin-bottom: 34px;

        .tt_event_hours_count {
            color: inherit;
            font-weight: inherit;
        }
	}

	.tt_event_hours li {
		border: none;
		background-color: #fbf9f9;
		padding: 36px 30px 35px;
		width: 48%;
		margin-right: 30px;
		margin-bottom: 30px;
		color: $default-text-color;
		text-transform: uppercase;
		box-sizing: border-box;

		&:nth-child(2n) {
			margin-right: 0;
		}

		h4 {
			margin: 0;
            color: $default-heading-color;
			font-family: $default-heading-font;
			font-size: 16px;
			line-height: 1.57em;
			font-weight: 500;
			letter-spacing: .06em;
			text-transform: uppercase;

			&:nth-of-type(1) {
				margin-top: 10px;
				margin-bottom: 6px;
			}

			&:nth-of-type(2) {
				font-size: 15px;
				font-weight: 400;
				letter-spacing: 0;
				color: $default-text-color;
			}
		}

		p {
			margin-top: 0;
			font-family: $default-text-font;
			font-weight: 400;
			font-size: 15px;
			letter-spacing: 0;
			text-transform: none;
			color: inherit;
			padding-top: 5px;
		}
	}

	.tt_event_items_list {
		margin: 0;


		li.type_info {
			border-bottom: 1px solid #f1f1f1;
			padding: 0;
            margin-bottom: 20px;

			p {
				display:inline;
			}

			.tt_event_text {
				font-family: $default-text-font;
				color: $default-text-color;
				font-size: 15px;
				font-weight: 400;
				line-height: 31px;
				text-align: left;
				margin-left: 20px;
			}

			label {
				font-family: $default-text-font;
				font-size: 16px !important;
				font-weight: 500 !important;
				text-transform: uppercase;
				color: $default-heading-color;
				line-height: 31px;
			}
		}

		li:not(.type_info) {
			background: none;
			font-family: $default-text-font;
			font-size: 16px;
			color: $default-heading-color;
			margin-bottom: 15px;
			padding-left: 27px;
			position: relative;

			&:before {
				content: '\4e';
				position: absolute;
				left: 0;

				font-family: 'ElegantIcons';
				color: $first-main-color;
				font-size: 18px;
			}
		}
	}

	.tt_event_page_left, .tt_event_page_right{
		padding-left: 25px;
		padding-right: 25px;
		box-sizing: border-box;
	}
}

.tt_event_page_left{
	width:75%;
}

.tt_event_page_right{
	width:25%;
}

.mkdf-grid-1200 {

	.tt_event_theme_page {
		width: 1200px;
	}
}

.mkdf-grid-1300 {

	.tt_event_theme_page {
		width: 1300px;
	}
}

@include ipad-portrait {
	.single-events {
		.tt_event_hours li {
			width: 47%;
		}
	}
}

@include phone-portrait {
	.single-events {
		.tt_event_hours li {
			width: 100%;
		}
	}

	.tt_event_items_list {
		li {
			.tt_event_text {
				width: auto;
			}
		}
	}
}

