#tribe-events-pg-template,
.tribe-events-pg-template {
	max-width: 100%;
}

.mkdf-tribe-events-single {
	@include mkdfRelativeHolderLayout();
	margin: 49px 0 89px !important;
	padding: 0 !important;

	.mkdf-events-single-main-info {
		position: relative;
		padding: 0;
		margin: 21px 0 1px;

		.mkdf-events-single-title-holder {
			position: relative;

			.mkdf-events-single-title {
				margin: 1px 0 0;
			}

			.mkdf-events-single-info-icon {
				display: inline-block;
				vertical-align: top;
				margin: 0 7px 0 0;
				color: $first-main-color;
				font-size: 16px;
				line-height: inherit;

				* {
					display: block;
					line-height: inherit;

					&:before {
						display: block;
						line-height: inherit;
					}
				}
			}

			.mkdf-events-single-cost {
				position: absolute;
				display: inline-block;
				vertical-align: top;
				right: 0;
				top: 44px;
				padding: 12px 48px;
				color: $default-heading-color;
				background-color: $first-main-color;
				border: 1px solid transparent;
				width: auto;
				outline: 0;
				font-size: 12px;
				font-weight: 500;
				letter-spacing: .25em;
				text-transform: uppercase;
				box-sizing: border-box;
				margin: 0;
				border-radius: 0;
			}
		}
	}

	.mkdf-events-single-main-content {
		@include mkdfRelativeHolderLayout();
		
		.tribe-events-event-image {
			margin: 0;
			height: 443px;
			overflow: hidden;
			
			img {
				display: block;
			}
		}
		
		.tribe-events-venue-map {
			width: 100%;
			margin: 0;
			padding: 0;
			float: none;
			border: 0;
			border-radius: 0;
			
			> div {
				height: 443px !important;
				filter: url('img/desaturate.svg#grayscale');
				-webkit-filter: grayscale(100%);
				-moz-filter: grayscale(100%);
				filter: gray;
				filter: grayscale(100%);
			}
		}
		
		.mkdf-events-single-content-holder {
			@include mkdfRelativeHolderLayout();
			margin: 27px 0 32px;

			.mkdf-events-single-cost-content {
				display: inline-block;
				padding: 12px 48px;
				color: $default-heading-color;
				background-color: $first-main-color;
				border: 1px solid transparent;
				width: auto;
				outline: 0;
				font-size: 12px;
				font-weight: 500;
				letter-spacing: .25em;
				text-transform: uppercase;
				box-sizing: border-box;
				margin: 0;
				border-radius: 0;
			}
		}
		
		.mkdf-events-single-content-title {
			display: block;
			margin: 99px 0 30px;
		}
		
		.mkdf-events-single-meta-item {
			margin: 0 0 14px;
			
			> * {
				display: inline-block;
				vertical-align: top;
				line-height: inherit;
				color: $default-text-color;

				a {
					color: inherit;
				}
			}
		}
		
		.mkdf-events-single-meta-icon {
			color: $default-heading-color;
			font-size: 22px;
			margin: 0 9px 0 0;
			min-width: 19px;
			display: inline-block;
			text-align: center;
			
			* {
				display: block;
				line-height: inherit;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.mkdf-events-single-meta-label {
			margin: 0;
		}
		
		.mkdf-events-single-navigation {
			@include mkdfRelativeHolderLayout();
			margin: 21px 0 0;

			.mkdf-events-single-prev,
			.mkdf-events-single-next {

				a {
					position: relative;
					color: $default-heading-color;
					font-size: 13px;
					font-weight: 500;
					line-height: 100px;
					letter-spacing: .13em;
					text-transform: uppercase;

					&:after {
						content: '';
						height: 1px;
						position: absolute;
						left: 0;
						bottom: -3px;
						display: block;
						width: 100%;
						background-color: $default-heading-color;
						@include mkdfTransform(scaleX(1));
						@include mkdfTransformOrigin(0 0);
						@include mkdfTransition(transform .3s ease-out);
					}

					&:before {
						content: '';
						height: 1px;
						position: absolute;
						left: 0;
						bottom: -3px;
						display: block;
						width: 100%;
						background-color: $default-heading-color;
						@include mkdfTransform(scaleX(0));
						@include mkdfTransformOrigin(100% 0);
						@include mkdfTransition(transform .3s ease-out .3s);
					}

					&:hover {
						&:after {
							@include mkdfTransform(scaleX(0));
						}

						&:before {
							@include mkdfTransform(scaleX(1));
						}
					}
				}
			}
			
			.mkdf-events-single-prev {
				float: left;
				text-align: left;
			}
			
			.mkdf-events-single-next {
				float: right;
				text-align: right;
			}
		}
	}
	
	.tribe-events-cal-links {
		display: none;
		text-align: right;
		margin: 55px 0 20px;
		
		#tribe-events & {
			
			a.tribe-events-button {
				@include mkdfButtonDefaultStyle();
				@include mkdfButtonMediumParams();
				margin: 0;
				font-weight: 500;
				line-height: 2em;
				
				&.tribe-events-gcal {
					@include mkdfButtonSolidColor();
					
					&:hover {
						@include mkdfButtonSolidHoverColor();
					}
				}
				
				&.tribe-events-ical {
					@include mkdfButtonSolidColor();
					color: #fff;
					background-color: $default-heading-color;
					margin: 0 0 0 15px;
					
					&:hover {
						@include mkdfButtonSolidColor();
					}
				}
			}
		}
	}
}

@media only screen and (min-width: 1025px) {

	.mkdf-tribe-events-single {
		.mkdf-events-single-main-content {
			.mkdf-events-single-content-holder {
				.mkdf-events-single-cost-content {
					display: none;
				}
			}
		}
	}
}
@include ipad-landscape {

	.mkdf-tribe-events-single {
		.mkdf-events-single-main-content {
			.mkdf-events-single-content-holder {
				.mkdf-events-single-cost-content {
					margin: 11px 0 0;
				}
			}
		}
	}
}

@include laptop-landscape-mac {

	.mkdf-tribe-events-single {

		.mkdf-events-single-main-content {

			.tribe-events-venue-map>div {
				height: 375px !important;
			}
		}
	}
}

@include laptop-landscape-medium {
	
	.mkdf-tribe-events-single {
		
		.mkdf-events-single-main-content {
			
			.tribe-events-venue-map>div {
				height: 374.5px !important;
			}
			
			.mkdf-events-single-meta-value {
				font-size: 14px;
			}
		}
	}
}

@include ipad-landscape {
	
	.mkdf-tribe-events-single {

		.mkdf-events-single-main-info {

			.tribe-events-venue-map>div {
				height: 443px !important;
			}

			.mkdf-events-single-title-holder {
				.mkdf-events-single-cost {
					display: none;
					top: 0;
				}
			}
		}
		
		.mkdf-events-single-main-content {
			
			.mkdf-events-single-map {
				margin: 50px 0 0;
			}
		}
	}
}

@include ipad-portrait {

	.mkdf-tribe-events-single {

		.mkdf-events-single-main-info {
			.mkdf-events-single-title-holder {
				.mkdf-events-single-cost {
					position: relative;
				}
			}
		}

		.mkdf-events-single-main-content .tribe-events-event-image {
			height: auto;
		}
	}
}

@include phone-landscape {
	
	.mkdf-tribe-events-single {
		
		.mkdf-events-single-main-info {
			
			.mkdf-events-single-title-holder {
				
				.mkdf-events-single-cost {
				}
			}
		}
	}
}