}

@media only screen and (max-width: 768px) {
  .single-events .tt_event_hours li {
    width: 47%;
  }
}

@media only screen and (max-width: 480px) {
  .single-events .tt_event_hours li {
    width: 100%;
  }
  .tt_event_items_list li .tt_event_text {
    width: auto;
  }
}

table.tt_timetable {
  font-weight: 400;
  color: #808080;
  font-size: 16px;
  border-collapse: collapse !important;
}

table.tt_timetable thead tr.row_gray {
  background-color: #fff !important;
  border: none;
}

table.tt_timetable tbody tr {
  border: none;
  background-color: #ffeee2 !important;
}

table.tt_timetable tbody tr:last-child {
  border-bottom: none !important;
}

table.tt_timetable tbody tr:last-child td {
  border-bottom: 1px solid #c8c8c8 !important;
}

table.tt_timetable td.tt_hours_column {
  font-size: 15px;
  font-weight: 400;
  line-height: 1.6em;
  letter-spacing: 0;
  font-family: "Montserrat", sans-serif;
  color: #6c6c6c;
}

table.tt_timetable th {
  color: #000;
  height: 76px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.6em;
  letter-spacing: 0.06em;
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
  border: 1px solid #c8c8c8 !important;
  border-bottom: none !important;
  padding: 12px 15px 10px;
}

table.tt_timetable td {
  font-weight: 400;
  color: #808080;
  font-size: 12px;
  height: 118px;
  border-left: 1px solid #c8c8c8 !important;
  border-right: 1px solid #c8c8c8 !important;
}

table.tt_timetable .row_gray {
  background-color: #fff !important;
}

table.tt_timetable .event_container {
  padding: 15px 10px 15px;
  box-sizing: border-box;
}

table.tt_timetable .event {
  background-color: transparent;
  color: #808080;
  vertical-align: middle;
}

table.tt_timetable .event .event_header {
  font-size: 13px;
  line-height: 1.7em;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  color: #000 !important;
  margin-bottom: 8px;
}

table.tt_timetable .event .top_hour,
table.tt_timetable .event .bottom_hour {
  display: inline-block;
  margin: 0;
  float: left;
  width: 50%;
  box-sizing: border-box;
}

table.tt_timetable .event .top_hour .hours,
table.tt_timetable .event .bottom_hour .hours {
  color: #6c6c6c !important;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0;
}

table.tt_timetable .event .top_hour {
  text-align: right;
  padding-right: 5px;
  position: relative;
  color: #6c6c6c;
}

table.tt_timetable .event .top_hour:after {
  position: absolute;
  content: "-";
  right: -2px;
  color: inherit;
}

table.tt_timetable .event .bottom_hour {
  text-align: left;
  padding-left: 5px;
}

table.tt_timetable .event .before_hour_text,
table.tt_timetable .event .after_hour_text {
  font-size: 15px;
  color: #6c6c6c;
}

table.tt_timetable .event .before_hour_text {
  display: none;
}

table.tt_timetable .event .after_hour_text {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 15px;
  letter-spacing: 0;
  color: #6c6c6c !important;
  margin-top: 5px;
}

table.tt_timetable .event:not(.tt_single_event) .event_container:last-child {
  border-bottom: none;
}

table.tt_timetable .event hr {
  display: none;
}

table.tt_timetable .event:hover, table.tt_timetable .event .event_container.tt_tooltip:hover {
  background-color: transparent;
}

table.tt_timetable .event.tt_tooltip:hover .hours, table.tt_timetable .event .event_container.tt_tooltip:hover .hours {
  color: #808080;
}

table.tt_timetable .event a:hover, table.tt_timetable .event a.event_header:hover {
  text-decoration: none;
}

table.tt_timetable .tt_tooltip_text {
  top: auto !important;
  left: 50% !important;
  bottom: 100% !important;
  border: 1px solid transparent;
  display: inline-block;
  height: auto !important;
  box-sizing: border-box;
  width: 133px;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  transform: translateX(-50%);
}

table.tt_timetable .tt_tooltip_text .tt_tooltip_content {
  color: #000;
  background-color: #fdd8d6;
  text-align: center;
  padding: 9px 18px 5px 18px;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: 600;
  box-sizing: border-box;
  border-radius: 0;
  letter-spacing: 1px;
}

table.tt_timetable .tt_tooltip_text .tt_tooltip_content a {
  display: none;
}

table.tt_timetable .tt_tooltip_text .tt_tooltip_arrow {
  border-color: #fdd8d6 transparent;
  border-width: 8px 8px 0;
  bottom: 0;
  z-index: 2;
  display: block;
  height: 0;
  width: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  transform: translateX(-50%);
}

.tabs_box_navigation.sf-timetable-menu .tabs_box_navigation_selected {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6em;
  letter-spacing: 2px;
  font-family: "Montserrat", sans-serif;
  color: #000;
  border: 1px solid #fdd8d6;
  background-color: #fdd8d6;
}

.sf-timetable-menu li ul li a:hover, .sf-timetable-menu li ul li.selected a:hover {
  background-color: #fdd8d6;
  color: #000 !important;
}

.tabs_box_navigation .tabs_box_navigation_icon {
  font-size: 12px;
  background: none;
  font-family: 'simple-line-icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.tabs_box_navigation .tabs_box_navigation_icon:after {
  content: "\e604";
}

.tt_tabs {
  text-align: center;
}

.tt_tabs .tt_navigation_wrapper .tt_navigation_cell {
  margin: 0 auto !important;
}

.tt_tabs .tt_tabs_navigation {
  display: inline-block;
  margin-bottom: 35px;
}

.tt_tabs .tt_tabs_navigation li {
  margin: 0 5px 5px 5px !important;
  float: none;
  display: inline-block;
}

.tt_tabs .tt_tabs_navigation li a {
  background-color: transparent;
  font-size: 12px !important;
  font-weight: 500;
  line-height: 1.6em;
  letter-spacing: 0.1em;
  font-family: "Montserrat", sans-serif;
  color: #000 !important;
  text-transform: uppercase;
  box-sizing: border-box;
  padding: 7px 28px !important;
  border: none !important;
  margin: 0;
  -webkit-transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
  -moz-transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
  transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
}

.tt_tabs .tt_tabs_navigation .ui-tabs-active a,
.tt_tabs .tt_tabs_navigation li a:hover {
  background-color: #fdd8d6;
  color: #000 !important;
}

.tt_tabs .tt_items_list.thin span.event_description {
  margin-top: 0;
  vertical-align: middle;
}

#mkdf-timetable-transparent table.tt_timetable .event {
  background: #302f34 !important;
}

#mkdf-timetable-transparent.tt_tabs .tt_tabs_navigation li a {
  color: #fff !important;
}

.tt_responsive .tt_timetable.small .tt_items_list div.value {
  width: 282px;
  text-align: center;
  color: #6c6c6c !important;
  font-family: "Montserrat", sans-serif;
  font-size: 11px;
  font-weight: 400;
  letter-spacing: 0;
}

@media only screen and (max-width: 680px) {
  .tt_responsive .tt_timetable.small .tt_items_list div.value {
    width: 120px;
    text-align: right !important;
  }
}

@media screen and (max-width: 479px) {
  .tt_responsive .tt_timetable.small.use_colors .tt_items_list div.event_container {
    width: 138px;
  }
}

@media only screen and (max-width: 1024px) {
  table.tt_timetable td.tt_hours_column, table.tt_timetable th {
    font-size: 12px;
  }
  table.tt_timetable .event .event_header {
    font-size: 13px;
    margin-bottom: 2px;
  }
  table.tt_timetable .event .bottom_hour .hours, table.tt_timetable .event .top_hour .hours {
    font-size: 11px;
  }
  table.tt_timetable .event_container {
    padding: 19px 5px 15px;
  }
  .tt_tabs .tt_tabs_navigation li a {
    padding: 7px 18px !important;
  }
  .tt_tabs .tt_timetable td {
    height: 100px;
  }
}

@media only screen and (max-width: 768px) {
  .tt_tooltip_content {
    padding: 4px 2px 5px;
  }
  .tt_tabs .use_colors .tt_items_list li {
    background-color: #fff;
  }
}

@media only screen and (max-width: 680px) {
  .tt_timetable.small.use_colors .tt_items_list li {
    padding: 12px 10px !important;
    background-color: #fff;
  }
  .tt_tooltip_content {
    padding: 4px 0 6px;
    text-align: left;
  }
  .event_container {
    text-align: left;
    margin-top: 4px;
  }
  .tt_items_list .value {
    margin-top: 4px;
  }
  .tt_responsive .tt_timetable.small .box_header {
    text-transform: uppercase;
    color: #000 !important;
    font-family: inherit;
  }
}

.widget.tt_upcoming_events_widget {
  border: none !important;
  padding: 0 !important;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events_widget:before, .widget.tt_upcoming_events_widget .tt_upcoming_events_widget:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}

.widget.tt_upcoming_events_widget .tt_upcoming_events_widget:after {
  clear: both;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events_widget {
  *zoom: 1;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events_wrapper {
  margin-top: 0;
}

.widget.tt_upcoming_events_widget .caroufredsel_wrapper {
  border: 1px solid #e5e5e5;
  position: relative;
  background-color: inherit;
  box-sizing: border-box;
}

.widget.tt_upcoming_events_widget .caroufredsel_wrapper:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  left: 0;
  bottom: 0;
  background-color: inherit;
  z-index: 1;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events {
  width: 100% !important;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events li {
  width: 100%;
  padding: 0 !important;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events .tt_upcoming_events_event_container {
  border: none !important;
  border-bottom: 1px solid #e5e5e5 !important;
  border-left: 3px solid transparent;
  font-size: 20px;
  height: 98px;
  padding: 25px 20px 20px;
  box-sizing: border-box;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events .tt_upcoming_events_event_container:hover {
  height: auto;
  background-color: #fdd8d6;
  color: white;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events .tt_upcoming_events_hours {
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
}

.widget.tt_upcoming_events_widget .tt_upcoming_events .tt_calendar_icon {
  float: none;
  display: inline-block;
  margin: 0 7px 0 0;
  vertical-align: middle;
}

.widget.tt_upcoming_events_widget .tt_upcoming_event_controls {
  margin-top: 30px;
}

.widget.tt_upcoming_events_widget .tt_upcoming_event_controls:before, .widget.tt_upcoming_events_widget .tt_upcoming_event_controls:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}

.widget.tt_upcoming_events_widget .tt_upcoming_event_controls:after {
  clear: both;
}

.widget.tt_upcoming_events_widget .tt_upcoming_event_controls {
  *zoom: 1;
}

.widget.tt_upcoming_events_widget .tt_upcoming_event_controls a {
  width: 39px;
  height: 39px;
  line-height: 39px;
  border: 1px solid #e5e5e5;
  text-align: center;
  padding: 0;
  box-sizing: border-box;
  -webkit-transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
  -moz-transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
  transition: background-color 0.2s ease-out, border-color 0.2s ease-out;
}

.widget.tt_upcoming_events_widget .tt_upcoming_event_controls a#upcoming_event_prev {
  margin-right: 13px;
}

.widget.tt_upcoming_events_widget .tt_upcoming_event_controls a:hover {
  background-color: #fdd8d6;
  border-color: #fdd8d6;
}

.widget.tt_upcoming_events_widget .tt_upcoming_event_controls span {
  display: inline-block;
}
