/* ==========================================================================
   Instagram widget style - begin
   ========================================================================== */
.widget.widget_arabesque_instagram_widget {
  margin: 0;
}

.mkdf-instagram-feed {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    float: left;
    box-sizing: border-box;
    border: none !important;

    a {
      position: relative;
      display: block;
      overflow: hidden;

      .mkdf-instagram-icon{
        position: absolute;
        top: 50%;
        left: 50%;
        opacity: 0;
        @include mkdfTransform(translateX(-50%)translateY(-50%));
        @include mkdfTransition(opacity .2s ease-in-out);

        i {
          font-size: 17px;
          line-height: 1;
          display: inline-block;
          vertical-align: middle;
          color: $default-heading-color;
        }
      }

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100.2%;
        width: 100.2%;
        background: -moz-linear-gradient(top, rgba(255,255,255,.2) 0%, rgba(255,255,255,1) 116%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(255,255,255,.2) 0%,rgba(255,255,255,1) 116%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(255,255,255,0) .2%,rgba(255,255,255,1) 116%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        opacity: 0;
        @include mkdfTransition(opacity .2s ease-in-out);
      }

      &:hover {

        &:before,
        .mkdf-instagram-icon{
          opacity: 1;
        }
      }
    }

    img {
      width: 100%;
      display: block;
    }
  }

  &.mkdf-instagram-gallery {

    $gallery_space_label: ('no', 'tiny', 'small', 'normal');
    $gallery_space_width: (0, 3, 10, 15);

    @for $i from 0 to length($gallery_space_label) {
      &.mkdf-#{nth($gallery_space_label,$i+1)}-space {
        $column_width: nth($gallery_space_width, $i+1);
        $column_width_margin: $column_width * 2;

        @if ($column_width == 0) {
          margin: 0;
        } @else {
          margin: 0 -#{$column_width}px -#{$column_width_margin}px;
        }

        li {
          padding: 0 #{$column_width}px;
          margin: 0 0 #{$column_width_margin}px;
        }
      }
    }

    $gallery_columns: ('2', '3', '4', '6', '9');
    @for $i from 0 to length($gallery_columns) {
      &.mkdf-col-#{nth($gallery_columns, $i+1)} {
        $column_width: 100%/($i+2);

        @if ($i == 3) {
          $column_width: 100%/6;
        } @else if ($i == 4) {
          $column_width: 100%/9;
        }

        li {
          width: $column_width;
        }
      }
    }
  }

  &.mkdf-instagram-carousel {

    li {
      position: relative;
      width: 100%;
      margin: 0;
      padding: 0;

      a {
        position: relative;
        display: block;
        height: 100%;

        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(#fff, .25);
          opacity: 0;
          @include mkdfTransition(opacity 0.3s ease-in-out);
        }

        &:hover {
          opacity: 1;
        }
      }
    }
  }
}

footer .mkdf-instagram-feed.mkdf-instagram-gallery.mkdf-tiny-space {
    width: 300px;
    display: block;
    margin: 0 auto;
}

@media only screen and (max-width: $ipad-portrait) {
  footer .widget {
    .mkdf-instagram-feed.mkdf-instagram-gallery {
      width: 60%;
      display: block;
      margin: 0 auto;
    }
  }
}

@media only screen and (max-width: $phone-landscape) {
  footer .widget {
    .mkdf-instagram-feed.mkdf-instagram-gallery {
      width: 100%;
    }
  }
}
/* ==========================================================================
   Instagram widget style - end
   ========================================================================== */