/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;
    color: $widget-text-color;

    a {
        color: $widget-link-color;

        &:hover {
            color: $widget-link-hover-color;
        }
    }

    .mkdf-widget-title-holder {
        margin-bottom:  $widget-title-margin;
        .mkdf-widget-title {
            color: $widget-heading-color;
            margin: 0;
        }

        .mkdf-separator {
            border-color: $widget-text-color;
            border-bottom-width: 3px;
            width: 30px;
        }
    }

    ul {
        padding: 0;
        margin: 11px 0 0 0;
        list-style: none;

        li {
            display: block;
            margin: 0;
            padding: 0 0 $widget-bottom-padding;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            margin-top: $widget-bottom-padding;

            li{
                padding-left: 15px;
            }
        }
    }

    // widgets with lists and animation on link hover
    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {

        ul {

            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;

                    abbr{
                        color: $widget-heading-color;
                        &:hover{
                            color: $widget-link-hover-color;
                        }
                    }
                }
            }
        }
    }

    // widgets with lists and w/o animation on link hover
    &.widget_recent_comments,
    &.widget_rss {

        .mkdf-widget-title {
            margin-bottom: 0;
        }
    }

    // calendar
    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: $widget-link-color;
            background-color: $widget-list-item-background-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // rss
    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }
        .rsswidget{
            display: inline-block;
        }
        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
            font-size: 15px;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    // search
    &.widget_search {
        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 15px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 22px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;
                @include mkdfTransition(color .2s ease-in-out);

                .ion-ios-search:before {
                    font-weight: 700;
                }
            }

            input.search-field {
                width: calc(100% - #{$widget-search-form-height});
                padding: 10px 0 10px 20px;

                @include mkdfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: $widget-list-item-background-color;
                color: $default-heading-color;
                cursor: pointer;
                font-size: 23px;
                width: $widget-search-form-height;
                padding: 12px 0;

                @include mkdfTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    line-height: inherit;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    // search post type
    &.mkdf-search-post-type-widget {
        .mkdf-post-type-search-field {
            border-radius: 0;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            background-color: transparent;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 16px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            position: relative;
            vertical-align: top;
            -webkit-appearance: none;
            padding: 0 25px 0 20px;
            width: 100%; /* width of loading icon */

            @include mkdfTransition(color .2s ease-in-out);

            @include mkdfPlaceholder {
                color: $widget-text-color;
            }
        }

        ul li {
            padding: 10px 0;
        }
    }

    // combobox
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include mkdfTransition(color .2s ease-in-out);

            option {
            }

            &:focus {
                background-color: $default-text-color;
            }
        }
        img{
            margin-bottom: $widget-bottom-padding;
        }

    }

    // tag cloud
    &.widget_tag_cloud {

        a {
            border-radius: 2px;
            color: $widget-text-color;
            display: inline-block;
            font-size: 15px !important;
            font-family: $default-text-font;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            margin: 0 10px 7px 0;
            padding: 0;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    &.mkdf-recent-post-widget{
        .mkdf-recent-posts{
            .mkdf-rp-item{
                margin: 0 0 28px;
                padding: 0;

                a{
                    display: table;
                    width: 100%;

                    .mkdf-rp-image,
                    .mkdf-rp-title{
                        display: table-cell;
                        vertical-align: middle;
                        img{
                            display: block;
                        }
                    }
                    .mkdf-rp-image{
                        width: 70px;
                    }
                    .mkdf-recent-posts-info{
                        display: table-cell;
                        vertical-align: middle;

                        &:not(:first-child) {
                            padding-left: 20px;
                        }
                    }

                    .mkdf-rp-title{
                        @include mkdfTransition(color .2s ease-in-out);
                        margin: 0 0 $widget-bottom-padding;
                    }

                    .mkdf-rp-date{
                        font-size: 12px;
                    }

                    &:hover{
                        .mkdf-rp-title{
                            color: $default-text-color;
                        }
                    }
                }
            }
        }
    }
}

.mkdf-footer-bottom-holder .widget a {
    color: #767676;

    &:hover {
        color: #767676;
    }
}