$timetable-background-color: #ffeee2;
$timetable-border-color: #c8c8c8;
$timetable-text-color: $default-text-color;
$timetable-heading-color: #000;

table.tt_timetable {
    font-weight: 400;
    color: #808080;
    font-size: 16px;
    border-collapse: collapse !important;

    thead tr.row_gray {
        background-color: #fff !important;
        border: none;
    }

    tbody tr {
        border: none;
        background-color: $timetable-background-color !important;
        &:last-child {
            border-bottom: none !important;

            td {
                border-bottom: 1px solid $timetable-border-color !important;
            }
        }
    }

    td.tt_hours_column {
        font-size: 15px;
        font-weight: 400;
        line-height: 1.6em;
        letter-spacing: 0;
        font-family: $default-heading-font;
        color: $timetable-text-color;
    }

    th {
        color: $timetable-heading-color;
        height: 76px;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 500;
        line-height: 1.6em;
        letter-spacing: 0.06em;
        font-family: $default-heading-font;
        box-sizing: border-box;
        border: 1px solid $timetable-border-color !important;
        border-bottom: none !important;
        padding: 12px 15px 10px;
    }

    td {
        font-weight: 400;
        color: #808080;
        font-size: 12px;
        height: 118px;
        border-left: 1px solid $timetable-border-color !important;
        border-right: 1px solid $timetable-border-color !important;
    }

    .row_gray {
        background-color: #fff !important; //important because plugin css is also using important
    }

    .event_container {
        padding: 15px 10px 15px;
        box-sizing: border-box;
    }

    .event {
        background-color: transparent;
        color: #808080;
        vertical-align: middle;

        .event_header {
            font-size: 13px;
            line-height: 1.7em;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            font-family: $default-heading-font;
            font-weight: 500;
            color: $default-heading-color !important;
            margin-bottom: 8px;
        }

        .top_hour,
        .bottom_hour {

            display: inline-block;
            margin: 0;
            float: left;
            width: 50%;
            box-sizing: border-box;

            .hours {
                color: $timetable-text-color !important;
                font-family: $default-heading-font;
                font-size: 15px;
                font-weight: 400;
                letter-spacing: 0;

            }
        }

        .top_hour {

            text-align: right;
            padding-right: 5px;
            position: relative;
            color: $timetable-text-color;

            &:after {
                position: absolute;
                content: "-";
                right: -2px;
                color: inherit;
            }
        }

        .bottom_hour {
            text-align: left;
            padding-left: 5px;
        }

        .before_hour_text,
        .after_hour_text {
            font-size: 15px;
            color: $timetable-text-color;
        }

        .before_hour_text {
            display: none;
        }

        .after_hour_text {
            display: inline-block;
            font-family: $default-text-font;
            font-weight: 400;
            font-size: 15px;
            letter-spacing: 0;
            color: $timetable-text-color !important;
            margin-top: 5px;
        }

        &:not(.tt_single_event) {
            .event_container {
                &:last-child {
                    border-bottom: none;
                }
            }
        }


        hr {
            display: none;
        }

        &:hover, .event_container.tt_tooltip:hover {
            background-color: transparent;
        }

        &.tt_tooltip:hover .hours, .event_container.tt_tooltip:hover .hours {
            color: #808080;
        }

        a:hover, a.event_header:hover {
            text-decoration: none;
        }
    }

    .tt_tooltip_text {
        top: auto !important;
        left: 50% !important;
        bottom: 100% !important;
        border: 1px solid transparent;
        display: inline-block;
        height: auto !important;
        box-sizing: border-box;
        width: 133px;
        @include mkdfTransform(translateX(-50%));

        .tt_tooltip_content {
            color: $default-heading-color;
            background-color: $first-main-color;
            text-align: center;
            padding: 9px 18px 5px 18px;
            margin: 0;
            font-family: $default-heading-font;
            font-size: 14px;
            font-weight: 600;
            box-sizing: border-box;
            border-radius: 0;
            letter-spacing: 1px;

            a {
                display: none;

            }
        }

        .tt_tooltip_arrow {
            border-color: $first-main-color transparent;
            border-width: 8px 8px 0;
            bottom: 0;
            z-index: 2;
            display: block;
            height: 0;
            width: 0;
            left: 50%;
            @include mkdfTransform(translateX(-50%));
        }
    }

}

.tabs_box_navigation.sf-timetable-menu .tabs_box_navigation_selected {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.6em;
    letter-spacing: 2px;
    font-family: $default-heading-font;
    color: $default-heading-color;
    border: 1px solid $first-main-color;
    background-color: $first-main-color;
}

.sf-timetable-menu li ul li a:hover, .sf-timetable-menu li ul li.selected a:hover {
    background-color: $first-main-color;
    color: $default-heading-color !important;
}

.tabs_box_navigation .tabs_box_navigation_icon {
    font-size: 12px;
    background: none;
    font-family: 'simple-line-icons';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.tabs_box_navigation .tabs_box_navigation_icon:after {
    content: "\e604";
}

.tt_tabs {
    text-align: center;

    .tt_navigation_wrapper .tt_navigation_cell {
        margin: 0 auto !important;
    }

    .tt_tabs_navigation {
        display: inline-block;
        margin-bottom: 35px;

        li {

            margin: 0 5px 5px 5px !important;
            float: none;
            display: inline-block;
        }

        li a {
            background-color: transparent;
            font-size: 12px !important;
            font-weight: 500;
            line-height: 1.6em;
            letter-spacing: 0.1em;
            font-family: $default-text-font;
            color: $default-heading-color !important;
            text-transform: uppercase;
            box-sizing: border-box;
            padding: 7px 28px !important;
            border: none !important;
            margin: 0;

            -webkit-transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
            -moz-transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
            transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
        }

        .ui-tabs-active a,
        li a:hover {
            background-color: $first-main-color;
            color: $default-heading-color !important;
        }
    }

    .tt_items_list.thin span.event_description {
        margin-top: 0;
        vertical-align: middle;
    }
}

#mkdf-timetable-transparent {

    table.tt_timetable {
        .event {
            background: #302f34 !important;
        }
    }

    &.tt_tabs .tt_tabs_navigation li a {
        color: #fff !important;
    }
}

.tt_responsive .tt_timetable.small .tt_items_list div.value {
    width: 282px;
    text-align: center;
    color: $default-text-color !important;
    font-family: $default-text-font;
    font-size: 11px;
    font-weight: 400;
    letter-spacing: 0;
}

@media only screen and (max-width: 680px) {
    .tt_responsive .tt_timetable.small .tt_items_list div.value {
        width: 120px;
        text-align: right !important;
    }
}

@media screen and (max-width: 479px) {
    .tt_responsive .tt_timetable.small.use_colors .tt_items_list div.event_container {
        width: 138px;
    }
}

@include ipad-landscape {
    table.tt_timetable {
        td.tt_hours_column, th {
            font-size: 12px;
        }

        .event .event_header {
            font-size: 13px;
            margin-bottom: 2px;
        }

        .event .bottom_hour .hours, .event .top_hour .hours {
            font-size: 11px;
        }

        .event_container {
            padding: 19px 5px 15px;
        }
    }

    .tt_tabs .tt_tabs_navigation li a {
        padding: 7px 18px !important;
    }

    .tt_tabs .tt_timetable td {
        height: 100px;
    }
}

@include ipad-portrait {
    .tt_tooltip_content {
        padding: 4px 2px 5px;
    }

    .tt_tabs .use_colors .tt_items_list li {
        background-color: #fff;
    }
}

@include phone-landscape {
    .tt_timetable.small.use_colors .tt_items_list li {
        padding: 12px 10px !important;
        background-color: #fff;
    }

    .tt_tooltip_content {
        padding: 4px 0 6px;
        text-align: left;
    }

    .event_container {
        text-align: left;
        margin-top: 4px;
    }

    .tt_items_list .value {
        margin-top: 4px;
    }

    .tt_responsive .tt_timetable.small .box_header {
        text-transform: uppercase;
        color: $default-heading-color !important;
        font-family: inherit;
    }
}