@media only screen and (min-width: $ipad-landscape-plus-pixel) {
	
	.post-type-archive-tribe_events {
		
		.mkdf-content .mkdf-content-inner > .mkdf-container > .mkdf-container-inner {
			padding: 80px 0 0;
		}
	}
}

.tribe-events-notices {
	border-radius: 0;
}

.tribe-list-widget li {
	list-style-type: none;
}

.mkdf-events-list {
	box-sizing: border-box;
	padding-left: 15px;
	padding-right: 15px;
	width: 100%;

	&.mkdf-has-box-shadow {

		.mkdf-events-list-item-holder {
			box-sizing: border-box;
			padding: 20px 20px 0 0;

			.mkdf-events-list-item-image-holder {
				background-color: $first-main-color;

				&:after {
					content: "";
					position: absolute;
					z-index: 20;
					top: -20px;
					left: 20px;
					width: 100%;
					height: 100%;
					background-color: inherit;
				}

				a {
					z-index: 50;

					@include mkdfImageEasingRight();
					@include mkdfImageEasingRightHover();

					&:after {
						content: none;
					}
				}
			}
		}
	}

	&.mkdf-light-skin {

		.mkdf-events-list-item-content {
			color: #b6b6b6;

			.mkdf-events-list-item-title-holder {
				.mkdf-events-list-item-title {
					a {
						color: #fff;
					}
				}
			}

			.mkdf-events-list-item-info .mkdf-events-item-info-icon i {
				color: #fff;
			}
		}
	}
}

.mkdf-events-list-item {
	margin: 0 0 30px;

	.mkdf-events-list-item-content {
		letter-spacing: 0.015em;
		color: $default-text-color;
	}
}

.mkdf-events-list-item-image-holder {
	position: relative;
	margin: 0 0 33px;
	
	img {
		display: block;
	}
	
	a {
		position: relative;
		display: block;
		
		&:after {
			@include mkdfAbsoluteHolderLayout();
			content: '';
			background-color: rgba(#fff, .4);
			opacity: 0;
			@include mkdfTransition(opacity .4s cubic-bezier(.38,1,.57,1));
		}
		
		&:hover {
			
			&:after {
				opacity: 1;
			}
		}
	}
}

span.tribe-events-list-separator-month {
	display: none !important;
}

#tribe-events-header ul.tribe-events-sub-nav {
	visibility: hidden;
}

.mkdf-events-list-item-date-holder {
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 65px;
	height: 65px;
	font-family: $default-heading-font;
	font-size: 20px;
	line-height: 1;
	font-weight: 700;
	text-align: center;
	color: #fff;
	background-color: $first-main-color;
	z-index: 1;
	box-sizing: border-box;

	.mkdf-events-list-item-date-day {
		display: block;
		margin: 11px 0 2px;
	}
}

@include ipad-portrait {
	.mkdf-events-list {
		.mkdf-events-list-item {

			&.mkdf-grid-col-ipad-portrait-12 {
				width: 50%;
				float: left;
			}
		}
	}
}

@include phone-landscape {

	.mkdf-events-list {
		.mkdf-events-list-item {

			&.mkdf-grid-col-ipad-portrait-12 {
				width: 100%;
				float: none;
			}
		}
	}
	
	.mkdf-events-list-item-date-holder {
		top: 0;
		left: 0;
		width: 50px;
		height: 50px;
		font-size: 17px;
		
		.mkdf-events-list-item-date-day {
			margin: 6px 0 2px;
		}
	}
}

.mkdf-events-list-item-title-holder {
	position: relative;
	padding: 0 95px 0 0;

	.mkdf-events-list-item-title {
		text-transform: uppercase;
	}

	.mkdf-events-list-item-price {
		position: absolute;
		top: 0;
		right: 0;
		padding: 0 30px;
		font-family: $default-heading-font;
		font-size: 12px;
		line-height: 28px;
		font-weight: 700;
		letter-spacing: .15em;
		text-transform: uppercase;
		color: #fff;
		background-color: $default-heading-color;
		text-align: center;
		box-sizing: border-box;
	}
}

@include phone-landscape {
	
	.mkdf-events-list-item-title-holder {
		padding: 0;
		
		.mkdf-events-list-item-title {
			margin: 0 0 14px;
		}
		
		.mkdf-events-list-item-price {
			position: relative;
			margin: 0 0 24px;
		}
	}
}

.mkdf-events-list-item-holder {
	
	.mkdf-events-list-item-info {
		@include mkdfRelativeHolderLayout();
		margin: 6px 0 7px;
		
		> div {
			position: relative;
			display: block;
			vertical-align: top;
			margin-bottom: 4px;

			> span:not(.mkdf-events-item-info-icon) {
				padding-left: 9px;
			}

			> span.tribe-event-time {
				padding-left: 0;
			}

			> span.qpdef-events-list-item-location {
				padding-left: 12px;
			}
		}

		> .mkdf-events-list-item-location-holder {
			> span.mkdf-events-item-info-icon {
				padding-left: 2px;
			}
		}
		
		.mkdf-events-item-info-icon {
			position: relative;
			display: inline-block;
			vertical-align: top;

			i {
				color: #000;
				font-size: 18px;
				line-height: 1;
			}
		}
	}
	
	.mkdf-events-list-item-title-holder {
		padding: 0;
	}
	
	.mkdf-events-list-item-meta {
		margin: 30px 0 0;
	}
	
	.mkdf-events-list-item-excerpt-holder {
		margin: 5px 0 0;
	}
}