@import 'variables';
@import 'mixins';

/* other */
a {
  &.mkdf-link-underline {
    @include mkdfLinkUnderline();
  }
}

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 41px 0 20px;
    }
	
	.mkdf-comment {
		
		&.mkdf-pingback-comment {
			
			.mkdf-comment-text {
				padding: 0;
			}
		}
	}

    .mkdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 0 0 0;
            padding: 24px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 34px;

        > * {
            margin: 0;
            font-size: 30px;
            line-height: 1.133em;
        }
    }

    .mkdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 100px;
        height: 100px;
        float: left;

        img {
            display: block;
        }
    }

    .mkdf-comment-text {
        padding: 4px 0 23px 124px;
        min-height: 82px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 0 0 15px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            margin-right: 10px;
            font-size: 12px;
            font-weight: 500;
            letter-spacing: 0.13em;
            text-transform: uppercase;
            color: #000;

            &:hover {
                color: $default-heading-color;
            }
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
			max-width: 80%;
            font-size: 20px;
            line-height: 1.2em;
        }

        .mkdf-comment-date {
            float: left;
            margin: 0 0 7px 0;
            font-size: 15px;
        }

        .mkdf-text-holder {
          margin-bottom: 15px;

          p {
            margin: 0;
          }
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 16px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 13px;
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .comment-form-cookies-consent {
            margin: 0 0 28px;
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .mkdf-comment-form-inner {
        padding: 39px 0 78px;

      .comment-form-cookies-consent {
        margin: 0 0 28px 0;
      }
    }
    
    .comment-reply-title {
        font-size: 30px;
        line-height: 1.133em;
        margin: 0 0 44px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .mkdf-comment-holder {
	    
        .mkdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {
	        
            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }

    .mkdf-comment-form {

        .comment-reply-title {
            font-size: 30px;
        }
    }
}

/* get in touch CF7 get in touch form style start */
