    .mkdf-btn.mkdf-btn-medium {
        width: 100%;
    }
}
/* get in touch CF7 get in touch form style end */

/* get in touch CF7 sign up touch form style start */
.arabesque-sign-up-form {

  .wpcf7-form-control.wpcf7-date,
  .wpcf7-form-control.wpcf7-number,
  .wpcf7-form-control.wpcf7-quiz,
  .wpcf7-form-control.wpcf7-select,
  .wpcf7-form-control.wpcf7-text,
  .wpcf7-form-control.wpcf7-textarea {
      padding: 19px 18px;
  }

  .wpcf7-form-control.wpcf7-checkbox .wpcf7-list-item {
      margin: 0 0 12px;
      position: relative;
  }

  .wpcf7-form-control.wpcf7-checkbox .wpcf7-list-item.last {
      margin-bottom: 0;
  }

  .wpcf7-form-control.wpcf7-text.wpcf7-email {
    margin: 0 0 21px;
  }

  .wpcf7-form-control.wpcf7-textarea {
      height: 95px !important;
      margin-bottom: 2px;
  }

  .wpcf7-form-control.wpcf7-submit {
      display: block;
      margin: 0 auto;
  }

  input[type="checkbox"] {
    opacity: 0;
  }

  span.wpcf7-list-item-label {
    margin-left: 17px;
  }

  span.wpcf7-list-item-label::before,
  span.wpcf7-list-item-label::after {
    position: absolute;
    left: 0;
    top: initial;
  }

  span.wpcf7-list-item-label::before {
    top: 3px;
  }

  span.wpcf7-list-item-label::after {
    top: 6px;
    left: 2px;
    color: #000;
    font-family: 'ElegantIcons';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
  }

  span.wpcf7-list-item-label::before {
    display: inline-block;
    height: 18px;
    width: 18px;
    border: 1px solid #c8c8c8;
  }

  input[type="checkbox"] + span.wpcf7-list-item-label::after {
    content: none;
  }

  input[type="checkbox"]:checked + span.wpcf7-list-item-label::after {
    content: "\4e";
  }
}
/* get in touch CF7 sign up touch form style end */

