/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.mkdf-slider-is-moving {
		
		.owl-item {
			
			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.mkdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 37px); // 37 adj. position according to design
                @include mkdfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
            height: 42px;
			@include mkdfTransform(translateY(-50%));
			@include mkdfTransition(color .2s ease-out);
			
			&:hover {
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 46px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 3.7%;
            @include mkdfSvgArrowAnimation(-8px);
		}
		
		.owl-next {
			right: 3.7%;
            @include mkdfSvgArrowAnimation();
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 2px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 15px;
				height: 1px;
				margin: 0 5px;
				background-color: #000;
				box-sizing: border-box;
                @include mkdfTransformOrigin(50% 50%);
                @include mkdfTransition(width .3s ease-out);
			}

			&.active {
				
				span {
                  width: 25px;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            img {
                border: 2px solid transparent;
                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .mkdf-wrapper {
        padding: 0 !important;
    }
    
    .mkdf-content {
        height: 100vh;
    }
	
	&.admin-bar {
		
		.mkdf-content {
			height: 100vh;
		}
	}

    .mkdf-content-inner {
	    @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
	    @include mkdfTableCellLayout();
        padding: 0 18%;
	    text-align: center;

        .mkdf-404-title {
          margin: 0;
          color: #dddcdc;
          font-family: $theme-custom-font;
          font-size: 110px;
          font-style: italic;
          font-weight: 300;
          line-height: 1em;
          text-align: left;
        }
	
	    .mkdf-404-subtitle {
          margin: 0;
          color: #000000;
          font-family: $default-heading-font;
          font-size: 60px;
          font-weight: 200;
          line-height: 1em;
          text-align: left;
        }
	    
	    .mkdf-404-text {
          margin: 25px 0 0;
          width: 49%;
          color: #000000;
          font-family: $default-text-font;
          font-size: 20px;
          text-align: left;
	    }

        .mkdf-404-button-holder {
          text-align: left;
          margin-top: 40px;

          .mkdf-btn-default-style {
            color: #000;
            border-color: #000;

            &:hover {
              color: #fff !important;
              border-color: #000  !important;
              background-color: #000  !important;
            }
          }
        }

		.mkdf-404-form{
			&.mkdf-404-form-light{
				.input-holder{
					border-color: #fff;

					input,
					button{
						color: #fff;
					}

					input.search-field {

						@include mkdfPlaceholder {
							color: #fff;
						}
					}

					button:hover{
						color: rgba(255,255,255,0.8);
					}
				}
			}

			.input-holder {
				width: 45%;
				min-width: 300px;
				border: 1px solid $default-border-color;
				margin: 30px auto 0;

				input,
				button {
					background-color: transparent;
					border: 0;
					border-radius: 0;
					box-sizing: border-box;
					color: $default-form-color;
					display: inline-block;
					float: left;
					font-family: $default-text-font;
					font-size: 16px;
					font-weight: inherit;
					height: 48px;
					line-height: 46px;
					outline: 0;
					position: relative;
					vertical-align: top;
					-webkit-appearance: none;

					@include mkdfTransition(color .2s ease-in-out);
				}

				input.search-field {
					width: calc(100% - 48px);
					padding: 10px 0 10px 21px;

					@include mkdfPlaceholder {
						color: $default-form-color;
					}
				}

				button {
					background: transparent;
					color: $default-heading-color;
					cursor: pointer;
					font-size: 16px;
					width: 48px;

					@include mkdfTransition(color .2s ease-in-out);

					&:hover {
						color: #000;
					}

					span {
						display: block;
						left: 0;
						line-height: 48px;
						position: absolute;
						top: 0;
						width: 100%;

						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
		}
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 42px;
    right: 30px;
    bottom: 10px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include mkdfTransition(opacity 0.3s ease);
    }

    &.mkdf-light {
      > span {
        color: #fff;
      }
    }

    > span {
		position: relative;
		display: block;
		text-align: center;
		color: $default-heading-color;
		overflow: hidden;
		@include mkdfDefaultTransition(color, background-color, border-color);

		i:first-child {
			@include mkdfTransform(translateY(0));
		}

		i:last-child {
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			height: 100%;
			line-height: inherit;
			@include mkdfTransform(translateY(0));
		}

        i {
            @include mkdfTransition(.15s ease .05s);
        }

        &:hover {

            i:first-child {
                @include mkdfTransform(translateY(-300%));
            }

            i:last-child {
                @include mkdfTransform(translateY(-100%));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }

    svg {
      g {
        @include mkdfTransition(transform $default-easing-svg-arrows-time $default-easing-svg-arrows);
      }
    }

    &:hover {
      svg {
        g:last-child {
          @include mkdfTransform(translateY(-8px));
        }
      }
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

