// widgets spacing
$widget-bottom-margin: 30px;
$widget-bottom-padding: 10px;
$widget-inner-padding: 10px;
$widget-title-margin: 31px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: $default-text-color;

// list items border
$widget-list-item-border-color: #dddcdc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #dddcdc;
$widget-heading-color: #fff;

// links
$widget-link-color: #fff;
$widget-link-hover-color: #fff;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

footer {
    @import '../widget-types/default-wordpress-widgets';

    .mkdf-blog-list-holder {

        &.mkdf-bl-simple {
            .mkdf-bli-content {

                .mkdf-post-title,
                .mkdf-post-title a {
                    color: #fff;
                }
            }
        }
    }

    .widget.mkdf-blog-list-widget {

        .mkdf-blog-list-holder {
            .mkdf-post-title {
                a {
                    @include mkdfLinkUnderline();
                }
            }
        }
    }
}