// widgets spacing
$widget-bottom-margin: 24px;
$widget-bottom-padding: 10px;
$widget-inner-padding: 10px;
$widget-title-margin: 31px;

// widget color
$widget-background-color: transparent;

// widget border
$widget-border-color: $default-border-color;
$widget-border-width: 0;
$widget-border-style: solid;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 10px;

// list items color
$widget-list-item-background-color: $first-main-color;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: $default-text-color;
$widget-heading-color: #000;

// links
$widget-link-color: #666666;
$widget-link-hover-color: $default-heading-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

.mkdf-side-menu {
    @import '../widget-types/default-wordpress-widgets';
    @import '../widget-types/default-wordpress-widgets-flat';
}